import { DOCUMENT } from '@angular/common';
import { Component, Inject, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AuthService } from 'src/app/core/services/auth.service';
import { environment } from 'src/environments/environment';

@Component({
   selector: 'app-home',
   templateUrl: './home.component.html',
   styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {
   production: boolean;
   element;
   userName: string;
   currentUser;
   constructor(@Inject(DOCUMENT) private document: any, private router: Router,
      private authFackservice: AuthService) {
      this.currentUser = JSON.parse(localStorage.getItem('currentUser'));
      let user: any = localStorage.getItem('currentUser') ? JSON.parse(localStorage.getItem('currentUser')) : '';
      this.userName = (user && user.UserName) ? user.UserName : ''
   }

   ngOnInit(): void {
      this.element = document.documentElement;
      this.production = environment.production
   }

   getRights(num) {
      let arr = this.currentUser.AUA_Code.split(',');
      return arr.includes(num);
   }

   logout() {
      // if (environment.defaultauth === 'firebase') {
      //    this.authService.logout();
      // } else {
      this.authFackservice.logout();
      // }
      this.router.navigate(['/account/login']);
   }

   /**
    * Fullscreen method
    */
   fullscreen() {
      document.body.classList.toggle('fullscreen-enable');
      if (
         !document.fullscreenElement && !this.element.mozFullScreenElement &&
         !this.element.webkitFullscreenElement) {
         if (this.element.requestFullscreen) {
            this.element.requestFullscreen();
         } else if (this.element.mozRequestFullScreen) {
            /* Firefox */
            this.element.mozRequestFullScreen();
         } else if (this.element.webkitRequestFullscreen) {
            /* Chrome, Safari and Opera */
            this.element.webkitRequestFullscreen();
         } else if (this.element.msRequestFullscreen) {
            /* IE/Edge */
            this.element.msRequestFullscreen();
         }
      } else {
         if (this.document.exitFullscreen) {
            this.document.exitFullscreen();
         } else if (this.document.mozCancelFullScreen) {
            /* Firefox */
            this.document.mozCancelFullScreen();
         } else if (this.document.webkitExitFullscreen) {
            /* Chrome, Safari and Opera */
            this.document.webkitExitFullscreen();
         } else if (this.document.msExitFullscreen) {
            /* IE/Edge */
            this.document.msExitFullscreen();
         }
      }
   }

   pos() {
      window.open('http://192.168.2.24/testposlive');
   }

}
